
/* 최상단 */
#top_f{position:-webkit-sticky; position: sticky; top:0; left:0; z-index: 99;  background-color: #fff }


.top{width:100%; height: 60px; min-width:320px; float: left; background-color: #fff; position: relative; }
.top p{float: left; height: 60px; line-height: 80px; text-align: center; color: transparent; }
.top p:nth-child(1){width:20%;background: url(../public/images/menu_hb.svg) no-repeat center; cursor: pointer;}
.top p:nth-child(2){width:60%;background: url(../public/images/logo.png) no-repeat center;  background-size: contain; line-height: 60px; font-size: 30px;}
.top p>a{color:transparent;}
.top p:nth-child(3){width:20%;background: url(../public/images/bucket_hb.svg) no-repeat center; line-height: 60px;}


/* 검색 */
.seach{clear: both; width:100%; height: 50px; border:none; border-bottom:2px solid #b1b1b1; background-color: #fff; outline:none; text-indent: 60px; background: url(../public/images/ico_search.svg) no-repeat 20px center; font-size:16px;}


/* 좌측 서브메뉴 활성화 */
#sub_menu_w.show_menu{left:0%;}
#sub_menu_w.hide_menu{left:-70%;}



/* 서브메뉴 */
#top_wrap{position: relative;}
#sub_menu{width:70%; margin:0 auto; position: relative; }

/* 서브메뉴 로고 */
#sub_logo>h1{width:120px; height:80px; margin:20px 0 40px 0; position: relative; }


/* 쇼핑하기~ 이솝 소개 */
#s_menu_w{margin:50px 0; padding-left: 10px;}
#s_menu_w ul li{height:60px; font-size:3.3vw;}
#s_menu_w ul li:hover{cursor:pointer; font-weight: bold;}
#s_menu_w ul li a{color:#000;}

/* 사인 인, ~ 위시리스트 */
#s_menu_b{margin:50px 0; padding-left:10px;}
#s_menu_b ul li{height:30px; margin-bottom:10px; font-size:2vw; padding-left:18%; line-height: 30px;}

#s_menu_b ul li:hover{cursor:pointer; font-weight: bold;}
#s_menu_b ul li:nth-child(1){background: url(../public/images/user-check.svg) no-repeat left center;}
#s_menu_b ul li:nth-child(2){background: url(../public/images/map-pin.svg) no-repeat left center; }
#s_menu_b ul li:nth-child(3){background: url(../public/images/shopping-cart.svg) no-repeat left center; }

#close{position: absolute; right:-35px; top:15px; font-size:35px; cursor: pointer;}


