/* 상단 메뉴바 */

#menu1_wrap{width: 95%; margin:0 auto; }
#menu_bar{background-color:#e4d4c7; width:100%; height:60px; white-space:nowrap; margin:30px 0; overflow:auto; }
#menu_bar>a{color:#000; margin-left:30px; line-height: 50px; letter-spacing:1px;}
#menu_bar>a:first-child{text-decoration: underline;}

#menu_bar>a:last-child{margin-right: 30px;} /* 스킨 케어 기프트 */
#menu_bar>a:hover{text-decoration: underline; cursor: pointer;}

#menu_bar::-webkit-scrollbar{height:10px;}/*스크롤바 높이*/
#menu_bar::-webkit-scrollbar-track{background-color:#ccc;}/*스크롤바 색상*/
#menu_bar::-webkit-scrollbar-thumb {background-color:#eee;}/*드래그 가능한 스크롤바 색상*/
#menu_bar::-webkit-scrollbar-thumb:active{background-color:#eee;}/*드래그 가능한 스크롤바 색상 위에서 마우스를 누를때 색상*/


/* 스킨 케어 제품 보기 */

#section1{overflow-x:hidden; width:100%;}
#section1>h2{font-size:5vw; margin:40px 0;} /* 스킨 케어 제품 보기 */

.menu_section{ width:380px; white-space:nowrap; margin:30px auto; overflow:auto;}


.menu_section::-webkit-scrollbar{height:10px;}/*스크롤바 높이*/
.menu_section::-webkit-scrollbar-track{background-color:transparent;}/*스크롤바 색상*/
.menu_section::-webkit-scrollbar-thumb {background-color:transparent;}/*드래그 가능한 스크롤바 색상*/
.menu_section::-webkit-scrollbar-thumb:active{background-color:transparent;}/*드래그 가능한 스크롤바 색상 위에서 마우스를 누를때 색상*/



.menu_section>div>a{color:#fff; padding:20px 10px; font-weight: bold; border: 1px solid #bbb; display: inline-block; width:170px; height:150px;  margin:1px 2px;}



.menu_section1>a:nth-child(1){background: url(../../public/images/menu1.webp) no-repeat center; background-size:cover;}
.menu_section1>a:nth-child(2){background: url(../../public/images/menu2.jpg) no-repeat center; background-size:cover;}
.menu_section1>a:nth-child(3){background: url(../../public/images/menu3.jpg) no-repeat center; background-size:cover;}

.menu_section2>a:nth-child(1){background: url(../../public/images/menu4.jpg) no-repeat center; background-size:cover;}
.menu_section2>a:nth-child(2){background: url(../../public/images/menu5.jpg) no-repeat center; background-size:cover;}
.menu_section2>a:nth-child(3){background: url(../../public/images/menu6.jpg) no-repeat center; background-size:cover;}

.menu_section3>a:nth-child(1){background: url(../../public/images/menu7.jpg) no-repeat center; background-size: cover;}
.menu_section3>a:nth-child(2){background: url(../../public/images/menu8.jpg) no-repeat center; background-size: cover;}
.menu_section3>a:nth-child(3){background: url(../../public/images/menu9.jpg) no-repeat center; background-size: cover;}

.menu_section4>a:nth-child(1){background: url(../../public/images/menu10.jpg) no-repeat center; background-size: cover;}
.menu_section4>a:nth-child(2){background: url(../../public/images/menu11.jpg) no-repeat center; background-size: cover;}
.menu_section4>a:nth-child(3){background: url(../../public/images/menu12.jpg) no-repeat center; background-size: cover;}



/* type_wrap 피부 타입 */
.type_wrap{margin:40px auto; height:550px;}
.type_wrap>h1{font-size:5vw; line-height: 1.5em;margin-bottom: 40px;}

.skin_type{float: left; margin:0 auto;}
.skin_type>div{float: left; width:47%; height:140px;  border-top: 1px solid #333; border-bottom: 1px solid #333; margin-bottom: 10px; cursor: pointer;}
.skin_type>div:hover{background-color: rgb(251, 240, 226);}

.type>p{padding-left: 10px;}
.type>p:nth-of-type(1){padding:20px 0; font-weight: bold;}
.type:hover p:nth-of-type(1){text-decoration: underline;}
.type>p:nth-of-type(2){font-size:15px;}


/* 좌측 컨텐츠- 중성, 지성, 노화 */
.type1, .type3, .type5{margin-right:15px;}