
/* 메인 이미지 */
#top_img{width:100%; height:0; padding-bottom:60%; background: url(../../public/images/Aesop_main.jpg) no-repeat center 0;background-size:cover; margin:0 auto; position: relative;}
#top_img>p{color:rgb(46, 28, 3); position: absolute; left:20px; top:48%; line-height:1.5em; font-size:4.2vw; }


/* 가장 가벼운 스킨케어~ 지속가능성 */
.con1>li{width: 100%; height:0; padding-bottom: 60%;}
.con1>li>p{font-size:5vw; position: absolute; left:20px; color:#fff;} /* 글자 */

/* 나를 위한 시간 */
.con1>li:nth-child(1){background: url(../../public/images/con1.jpg) no-repeat center 0; background-size:cover; position: relative;} 
.con1_1{top:30%;}
.con1>li>p.con1_2{top:45%; font-size:3.5vw;}
.con1_3{top:75%;}

/* 버튼- 뷰 모어 */
.con1_3>button{border:1px solid #eaeaea; width: 130px; height:40px; cursor: pointer; box-shadow: 1px 1px 3px grey; transition:all 0.3s ease-in-out; border-radius: 10px; }
.con1_3>button:hover{font-weight: bold; color:#fff; background: url(../../public/images/go.png) no-repeat 95% center; }


/* 페이셜 어포인트먼트 */
.con1>li:nth-child(2){background: url(../../public/images/con1_1.jpg) no-repeat center 0; background-size:cover; position: relative;}


/* 지속가능성 */
.con1>li:nth-child(3){background: url(../../public/images/con1_3.jpg) no-repeat center 0; background-size:cover; position: relative;}
.con1>li>p.con1_2_1{top:52%; font-size:3vw; line-height: 1.5em;}



