/* 공통 css*/
/* 스토어 로케이터, 무료배송.. 등 */
#title{margin-top:100px; margin-left:4%; clear: both;}
#title>p{margin-bottom:30px;}
#title>p:nth-of-type(1){font-size:5vw; margin-bottom: 50px; font-weight: bold; } 
#title>p:nth-child(2){line-height: 1.5em; width: 95%; }
#title>p:nth-child(3){margin-top:50px; border:1px solid #eaeaea; width: 80%; height:60px; line-height: 60px; text-indent: 5%; position: relative; transition:ease-in-out 0.5s;}
#title>p:nth-child(3):hover{background: url(../../public/images/go.png) no-repeat 95% center; cursor: pointer; background-color:rgb(178, 148, 108);}