
#menu2_wrap{width: 95%; margin:0 auto;}
#menu_bar{background-color:#e4d4c7; width:100%; height:60px; white-space:nowrap; margin:30px 0; overflow:auto; }
#menu_bar>a{color:#000; margin-left:30px; line-height: 50px; letter-spacing:1px;}
#menu_bar>a:first-child{text-decoration: underline;}

#menu_bar>a:last-child{margin-right: 30px;} /* 스킨 케어 기프트 */
#menu_bar>a:hover{text-decoration: underline; cursor: pointer;}

#menu_bar::-webkit-scrollbar{height:10px;}/*스크롤바 높이*/
#menu_bar::-webkit-scrollbar-track{background-color:#ccc;}/*스크롤바 색상*/
#menu_bar::-webkit-scrollbar-thumb {background-color:#eee;}/*드래그 가능한 스크롤바 색상*/
#menu_bar::-webkit-scrollbar-thumb:active{background-color:#eee;}/*드래그 가능한 스크롤바 색상 위에서 마우스를 누를때 색상*/



/* 트래블 사이즈 필수품 */
.t_item{overflow-x:hidden; width:100%;}
.t_item>h2{font-size:5vw; margin:50px 0; padding-left: 20px;}
.t_item>p{margin:50px 0; line-height:1.5em; padding-left: 20px;}


/* 이미지 */
.trevel{display: inline-block; width: 320px; white-space:nowrap; overflow:auto; margin-left:30px;}
.trevel>a{display: inline-block; margin-right:15px; background-color: rgb(238, 210, 174); color:#000; width:290px; height:320px; position: relative; border-radius: 15px;}
.trevel_item1{ background: url(../../public/images/trevel1.png) no-repeat center 10px; background-size: contain;}
.trevel_item2{ background: url(../../public/images/trevel2.png) no-repeat center; background-size: contain;}
.trevel_item3{background: url(../../public/images/trevel3.png) no-repeat center 10px; background-size: contain;}


.trevel_txt{width:290px; position: absolute; left:0; bottom:10px; text-align: center;}



/* 트래블 사이즈 필수품 스크롤바 */
.trevel::-webkit-scrollbar{height:10px;}/*스크롤바 높이*/
.trevel::-webkit-scrollbar-track{background-color:transparent;}/*스크롤바 색상*/
.trevel::-webkit-scrollbar-thumb {background-color:transparent;}/*드래그 가능한 스크롤바 색상*/
.trevel::-webkit-scrollbar-thumb:active{background-color:transparent;}/*드래그 가능한 스크롤바 색상 위에서 마우스를 누를때 색상*/


