
/* 피부를 위한 집중 케어 제품 #product*/
#product_w{width: 91%; margin-top:100px; margin-left:3%; clear: both;}
#product_w>h2{font-size:5vw; margin-bottom: 50px; font-weight: bold; }
#product_w>p{line-height: 1.5em; margin-bottom: 50px;}


/* 제품 이미지 */
.pro>div{height:300px; position: relative; width:98%;}
.pro>div:hover{cursor: pointer;}
.pro>div:hover p:first-child{text-decoration: underline;}


#pro1{background:url(../../public/images/product1.png) no-repeat center 40px; background-size: contain; }
#pro2{background:url(../../public/images/product2.png) no-repeat  center 20px; background-size: 70%;}
#pro3{background:url(../../public/images/product3.png) no-repeat  center 40px; background-size: contain;}
#pro4{background:url(../../public/images/product4.webp) no-repeat  center 40px; background-size: contain;}
#pro5{background:url(../../public/images/product5.webp) no-repeat  center 20px; background-size: 70%;}


/* 제품명,설명 */
.pro_txt{width: 100%; height:60px; position:absolute; left:0; bottom:40px; font-weight: bold; text-align: center; font-size:16px;}
.pro_txt2{width: 100%;  height:20px; position:absolute; left:0; bottom:30px; text-align: center; letter-spacing: -2px; font-size:13px;}





