.bg{width: 100%; height:0; padding-bottom: 60%; background: url(../../public/images/Aesop_bg.webp) no-repeat center 0; background-size:cover; font-size:0;}/* 배경 이미지 */


/* reserve_wrap */
.reserve_wrap{width: 95%; padding-left: 10px;}

/* 컨설테이션 예약하기 */
.reserve{font-size: 4.8vw; margin: 50px auto 20px auto; width:100%; text-align: center;}
.reserve2{text-align: center; font-size:15px; line-height:1.5em; margin-bottom: 50px; width:100%;}


/* 현재위치에서 찾기 */
.shop{margin:50px 0; border:1px solid #eaeaea; width: 80%; height:60px; line-height: 60px; text-indent: 5%; position: relative; transition:ease-in-out 1.1s; }
.shop:hover{background: url(../../public/images/go.png) no-repeat 95% center; cursor: pointer; background-color:rgb(178, 148, 108); }


.search{width:100%; height: 50px; border:none; border-bottom:2px solid #b1b1b1; background-color: #fff; outline:none; text-indent: 10px;  font-size:16px; margin: 20px 0 100px 0;}
